.contact-us {
  &__info {
    margin: 0 0 24px;

    &--last {
      margin: 0 0 32px;
    }
  }

  &__info-title {
    margin: 0 0 8px;

    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__info-link {
    color: inherit;
    text-decoration: none;

    &:hover {
      color: #9fadc2;
      border-bottom: 1px solid $textColor;
    }
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__button {
    margin-bottom: 46px;

    @include onTablet {
      margin-bottom: 0;
    }
  }
}
